import React, { useRef, useState } from "react";
import { Link, useNavigate } from "react-router-dom";
import { useCookies } from "react-cookie";
import styled from "styled-components";
import axios from "axios";
import moment from "moment";
import Footer from "../../components/layout/Footer";

//  form 
const LoginBox = styled.div`
  margin: 20px auto;
  padding: 70px 0px;
  width: 90%;
  background : none;
  border-radius: 20px;
`;

// 
const Title = styled.div`
font-family: "Poppins";
  text-align: center;
  height: 45px;
  line-height: 45px;
  margin-bottom: 150px;
  font-size: 63px;
  font-weight: 850;
  & a{
    font-family: "Poppins";
    color : #38d9a9;
  }
`;

//  
const SubTitle = styled.div`
  text-align: center;
  font-size: 18px;
  font-weight: 600;
  color : #adb5c2;
`;


//   
const ForgotPassword = styled.span`
  margin-top: -15px;
  display: block;
  text-align: center;
  color : #38d9a9;
`;

//  
const InputBox = styled.input`
  display: block;
  margin: 10px auto;
  height: 40px;
  background: #ffffff;
  /* border: 1px solid #dddddd; */
  border: none;
  background: #f6f6f6;
  border-radius: 10px;
  color:#333333;
  font-size: 17px; 
  outline: none;
  padding: 0px 3%;
  width: 60%;
  &::placeholder {
      color: #aaaaaa; 
      font-size: 17px;
  }
  &:focus {
    border-color: #38d9a9;
  }
`;

//  
const SubmitBtn = styled.button`
  display: block;
  margin: 30px auto;
  height: 40px;
  background: #efefef;
  border: none;
  border-radius: 10px;
  background: #38d9a9;
  font-weight: bold;
  color:#ffffff;
  font-size: 18px; 
  outline: none;
  width: 66%;
  &::placeholder {
      color: #aaaaaa; 
      font-size: 18px;
  }
`;

const CouncilSignIn = () => {
  const navigate = useNavigate(); //   
  const [, setCookie] = useCookies(); //   

  //   
  const useridRef = useRef();
  const passwordRef = useRef();

  //    
  const [userid, setUserid] = useState("");
  const [password, setPassword] = useState("");

  //   
  const handleLogin = async (e) => {
    e.preventDefault();

    //    
    if (userid.length < 1) {
      window.alert(" .");
      useridRef.current.focus();
      setUserid('');
      return;
    }
    if (password.length < 1) {
      window.alert(" .");
      passwordRef.current.focus();
      setPassword('');
      return;
    }

    try {
      //  api 
      const loginResponse = await axios.post("http://" + process.env.REACT_APP_BACK_URL + "/login",
        {
          userid,
          password
        }
      );

      // 
      if (loginResponse.data.code === 200) {
        // 2    
        const expires = moment().add(2, "hours").toDate();
        setCookie("token", loginResponse.data.data.token, {
          path: "/",
          expires: expires,
        });
        setCookie("userId", loginResponse.data.data.userId, {
          path: "/",
          expires: expires,
        });
        setCookie("nickname", loginResponse.data.data.nickname, {
          path: "/",
          expires: expires,
        });
        setCookie("roles", loginResponse.data.data.roles, {
          path: "/",
          expires: expires,
        });
        setCookie("certification", loginResponse.data.data.certification, {
          path: "/",
          expires: expires,
        });
        setCookie("id", loginResponse.data.data.id, {
          path: "/",
          expires: expires,
        });
        navigate("/council/manage");
      } else if (loginResponse.data.code === 404) { 
        window.alert("  .");
      } else if (loginResponse.data.code === 401) { 
        window.alert("  .");
      }

    } catch (error) {
    
      console.error(" :", error);
    }
  };

  //      
  const activeEnter = (event) => {
    if (event.code === 'Enter') {
      handleLogin(event);
    }
  };

  return (
    <div>
      <LoginBox>
        {/*  */}
        <Title>
          <SubTitle>
             
          </SubTitle>
          <Link to={"/"}>Baram</Link>
        </Title>

        {/*  */}
        <InputBox
          type="text"
          ref={useridRef}
          name="id"
          value={userid}
          placeholder=""
          onChange={(e) => {
            setUserid(e.target.value);
          }}
          onKeyDown={(e) => { activeEnter(e) }}
        />

        {/*  */}
        <InputBox
          type="password"
          ref={passwordRef}
          name="password"
          value={password}
          placeholder=""
          onChange={(e) => {
            setPassword(e.target.value);
          }}
          onKeyDown={(e) => { activeEnter(e) }} />

        {/*   */}
        <SubmitBtn onClick={handleLogin}></SubmitBtn>
        <Link to={"/council/forgot"}>
          <ForgotPassword>/ ?</ForgotPassword>
        </Link>
      </LoginBox>
    </div>
  );
};

export default CouncilSignIn;