const AdminCouncilCreation = () => {
    const [groupedCouncilList, setGroupedCouncilList] = useState([]); //   
    const [key, setKey] = useState(0);
    const [name, setName] = useState("");
    const [college, setCollege] = useState(" ");
    const [location, setLocation] = useState("");
    const [operatingHours, setOperatingHours] = useState("");
    const [usageGuidelines, setUsageGuidelines] = useState("");
    const [file, setFile] = useState(null);
    const [cookies] = useCookies(); //   
    const navigate = useNavigate(); //   

    const handleFileChange = (event) => {
        const file = event.target.files[0];
        setFile(file);
    };
    useEffect(() => {
        if (!cookies.token) {
            navigate("/signin");
            return;
        }
        if (cookies.roles != "ADMIN") {
            navigate("/");
            return;

        }
        const fetchCouncils = async () => {
            try {
                //      
                if (!cookies.token) {
                    navigate("/signin");
                    return;
                }

                //     api 
                const response = await axios.get("http://" + process.env.REACT_APP_BACK_URL + "/councils/all", {
                    headers: {
                        Authorization: `Bearer ${cookies.token}`,
                    },
                });

                const groupedData = response.data.data.reduce((acc, item, index) => {
                    const key = item.college;
                    if (index !== 0 && key !== response.data.data[index - 1].college) {
                        acc.push([]);
                    }
                    acc[acc.length - 1].push(item);
                    return acc;
                }, [[]]);

                setGroupedCouncilList(groupedData);
                console.log(groupedData);

            } catch (error) {
                console.error(" :", error);
            }
        };

        fetchCouncils();
    }, [cookies.token, navigate, key]); // []    .

    const handleAddCouncil = async (e) => {
        e.preventDefault();

        //    
        if (name.length < 1) {
            window.alert(" .");
            setName('');
            return;
        }
        if (college == " ") {
            window.alert(" .");
            return;
        }
        if (location.length < 1) {
            window.alert(" .");
            setLocation('');
            return;
        }
        if (operatingHours.length < 1) {
            window.alert(" .");
            setOperatingHours('');
            return;
        }

        if (usageGuidelines.length < 1) {
            window.alert(" .");
            setUsageGuidelines('');
            return;
        }
        try {

            const formData = new FormData();
            formData.append('request', new Blob([JSON.stringify({
                name,
                college,
                location,
                operatingHours,
                usageGuidelines
            })],
                {
                    type: "application/json"
                }));
            formData.append('pic', file);
            const signUpResponse = await axios.post("http://" + process.env.REACT_APP_BACK_URL + "/manage/council",
                formData,
                {
                    'Content-Type': 'multipart/form-data',
                    Authorization: `Bearer ${cookies.token}`,
                }
            );
            // 
            if (signUpResponse.data.code === 200) {
                window.alert(" ");
                // navigate("/");
            }
        } catch (error) {
            console.error(" :", error);

        }
    };
    return (
        <AdminBox>
            <Header headerType={"noChatIcon"} headerText={" "}></Header>
            <ContentBox>
                <SubTitle> </SubTitle>
                <InputBox type="text" name="name" placeholder="  (ex )"
                    value={name}
                    onChange={(e) => {
                        setName(e.target.value);
                    }}
                />

                <Details>
                    <Summary isDefault={college == " "}>
                        {college}
                    </Summary>
                    <OptionList>
                    <OptionItem>
                            <Button onClick={() => setCollege("G*")}>
                                
                            </Button>
                        </OptionItem>
                        <OptionItem>
                            <Button onClick={() => setCollege("G")}>
                                
                            </Button>
                        </OptionItem>
                        <OptionItem>
                            <Button onClick={() => setCollege("G")}>
                                
                            </Button>
                        </OptionItem>
                        <OptionItem>
                            <Button onClick={() => setCollege("G")}>
                                
                            </Button>
                        </OptionItem>
                        <OptionItem>
                            <Button onClick={() => setCollege("G")}>
                                
                            </Button>
                        </OptionItem>
                        <OptionItem>
                            <Button onClick={() => setCollege("G")}>
                                
                            </Button>
                        </OptionItem>
                        <OptionItem>
                            <Button onClick={() => setCollege("G")}>
                                
                            </Button>
                        </OptionItem>
                        <OptionItem>
                            <Button onClick={() => setCollege("GIT")}>
                                IT
                            </Button>
                        </OptionItem>
                        <OptionItem>
                            <Button onClick={() => setCollege("G")}>
                                
                            </Button>
                        </OptionItem>
                        <OptionItem>
                            <Button onClick={() => setCollege("G")}>
                                
                            </Button>
                        </OptionItem>
                        <OptionItem>
                            <Button onClick={() => setCollege("G")}>
                                
                            </Button>
                        </OptionItem>
                        <OptionItem>
                            <Button onClick={() => setCollege("G")}>
                                
                            </Button>
                        </OptionItem>
                        <OptionItem>
                            <Button onClick={() => setCollege("M")}>
                                
                            </Button>
                        </OptionItem>
                        <OptionItem>
                            <Button onClick={() => setCollege("M")}>
                                
                            </Button>
                        </OptionItem>
                        <OptionItem>
                            <Button onClick={() => setCollege("M")}>
                                
                            </Button>
                        </OptionItem>
                        <OptionItem>
                            <Button onClick={() => setCollege("M")}>
                                
                            </Button>
                        </OptionItem>
                    </OptionList>
                </Details>

                <InputBox type="text" name="location" placeholder=" (ex AI 500)"

                    value={location}
                    onChange={(e) => {
                        setLocation(e.target.value);
                    }}
                />
                <InputBox type="text" name="operatingHours" placeholder=" (ex 9 ~ 16)"
                    value={operatingHours}
                    onChange={(e) => {
                        setOperatingHours(e.target.value);
                    }}
                />
                <TextareaBox
                    type="text"
                    // ref={passwordRef}
                    name="usageGuidelines"
                    placeholder=" (ex  )"
                    onChange={(e) => {
                        setUsageGuidelines(e.target.value);
                    }}
                />

                <FileInputBtn for="file">
                    <div>  </div>
                </FileInputBtn>
                <FileInputBox type="file" name="file" id="file" onChange={handleFileChange} />
                {/*   */}
                <SummitBtn onClick={handleAddCouncil}></SummitBtn>
            </ContentBox>
        </AdminBox>
    );
};

export default AdminCouncilCreation;